import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { Router, ActivatedRoute } from '@angular/router';
import { Store } from '@ngxs/store';
import { UtilsService } from '../../@core/utils.service';
import { Camera } from '@ionic-native/camera/ngx';
import { NavigationExtrasService } from '../../@core/navigation-extras.service';
import { FormBuilder, Validators } from '@angular/forms';
import { EditOrganizationInfoBasePage } from '../edit-organization-info-base.page';
import { IOrganizationInfo } from '../../@shared/interfaces/organization-info';
import { Constants } from '../../@global/constants';
import { FormCustomValidators } from '../../@shared/validators/form-custom-validators';
import { UpdateOrganizationInput } from '../../../API';
import { InformService } from '../../@core/inform.service';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-edit-sport-organization-info',
  templateUrl: './edit-sport-organization-info.page.html',
  styleUrls: ['./edit-sport-organization-info.page.scss'],
})
export class EditSportOrganizationInfoPage extends EditOrganizationInfoBasePage implements OnInit {
  public maxlength = Constants.MAX_LENGTH_PROFILE;
  constructor(
    location: Location,
    router: Router,
    store: Store,
    utilsService: UtilsService,
    route: ActivatedRoute,
    camera: Camera,
    informService: InformService,
    navigationExtrasService: NavigationExtrasService,
    translateService: TranslateService,
    private formBuilder: FormBuilder
  ) {
    super(
      location,
      router,
      store,
      utilsService,
      camera,
      informService,
      route,
      navigationExtrasService,
      translateService
    );
  }

  protected initForm(data: IOrganizationInfo) {
    this.formGroup = this.formBuilder.group({
      organizationName: [data.complete_name, [Validators.required, Validators.maxLength(this.maxlength)]],
      tagline: [data.tagline, [Validators.maxLength(this.maxlength)]],
      website: [data.website, [Validators.maxLength(this.maxlength), FormCustomValidators.validUrl]],
      description: [data.description, [Validators.maxLength(this.descriptionMaxlength)]],
      employeesSize: [data.employees_size?.id],
      phone: [
        data.phone,
        [Validators.pattern(Constants.PHONE_PATTERN), Validators.maxLength(Constants.MAX_LENGTH_USERNAME_COGNITO)],
      ],
    });
  }

  protected getOrganizationInput(): UpdateOrganizationInput {
    return {
      complete_name: this.formGroup.get('organizationName').value || '',
      description: this.formGroup.get('description').value || '',
      tagline: this.formGroup.get('tagline').value || '',
      website: this.formGroup.get('website').value || '',
      employees_size: this.formGroup.get('employeesSize').value || '',
      // TODO photo: this.formGroup.get('organizationName').value,
      phone: this.formGroup.get('phone').value || '',
      ...this.locationData,
    };
  }
}
